import React, { Component } from 'react';
import { Button, Modal, ModalBody } from 'reactstrap';
import { connect } from 'react-redux';
import axios from 'axios';
import Spinner from '../../Spinner/Spinner';
import { resetIngredients } from '../../../redux/actionCreators';

const mapStateToProps = state => {
    return {
        ingredients: state.ingredients,
        totalPrice: state.totalPrice,
        purchasable: state.purchasable,
        userId: state.userId,
        token: state.token,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        resetIngredients: () => dispatch(resetIngredients()),
    }
}

class Checkout extends Component {
    state = {
        values: {
            deliveryAddress: "",
            phone: "",
            paymentType: "Cash On Delivery",
        },
        isLoading: false,
        isModalOpen: false,
        ModalMsg: "",
    }

    goBack = () => {
        this.props.history.goBack("/");
    }
    inputChangerHandler = (e) => {
        this.setState({
            values: {
                ...this.state.values,
                [e.target.name]: e.target.value,
            }
        })
    }

    submitHandler = () => {
        this.setState({
            isLoading: true,
        })
        const order = {
            ingredients: this.props.ingredients,
            customer: this.state.values,
            price: this.props.totalPrice,
            orderTime: new Date(),
            userId: this.props.userId,
        }
        axios.post("https://burger-builder-c1cd1-default-rtdb.firebaseio.com/orders.json?auth=" + this.props.token, order)
            .then(response => {
                if (response.status === 200) {
                    this.setState({
                        isLoading: false,
                        isModalOpen: true,
                        ModalMsg: "Order Placed Successfully!",
                    })
                    this.props.resetIngredients();
                } else {
                    this.setState({
                        isLoading: false,
                        isModalOpen: true,
                        ModalMsg: "Something Went Wrong! Order Again! ",
                    })
                }
            })
            .catch(err => {
                this.setState({
                    isLoading: false,
                    isModalOpen: true,
                    ModalMsg: "Something Went Wrong! Order Again! ",
                })
            })
        // console.log(this.state.values);
        // console.log(order);
    }

    render() {
        let form = (<div>
            <h4 style={{
                border: "1px solid Red",
                boxShadow: "1px 1px #888888",
                borderRadius: "5px",
                padding: "20px",
            }}>Payment:{this.props.totalPrice} BDT</h4>
            <form style={{
                border: "1px solid Red",
                boxShadow: "1px 1px #888888",
                borderRadius: "5px",
                padding: "20px",
            }}>
                <textarea name="deliveryAddress" value={this.state.values.deliveryAddress} className="form-control" onChange={(e) => this.inputChangerHandler(e)} placeholder="Your Address"></textarea>
                <br />
                <input name="phone" className="form-control" value={this.state.values.phone} onChange={(e) => this.inputChangerHandler(e)} placeholder="Phone Number"></input>
                <br />
                <select name="paymentType" className="form-control" value={this.state.values.paymentType} onChange={(e) => this.inputChangerHandler(e)}>
                    <option values='Cash On Delivery'>Cash On Delivery</option>
                    <option values='Bkask'>Bkash</option>
                </select>
                <br />
                <Button style={{ backgroundColor: "#D70F64" }} className="me-auto" onClick={this.submitHandler} disabled={!this.props.purchasable}>Place Order</Button>
                <Button style={{ backgroundColor: "secondary" }} className="ml-1" onClick={this.goBack}>Cancel</Button>
            </form>
        </div>)
        return (
            <div>
                {this.state.isLoading ? <Spinner /> : form}
                <Modal isOpen={this.state.isModalOpen} onClick={this.goBack}>
                    <ModalBody>
                        <p>{this.state.ModalMsg}</p>
                    </ModalBody>

                </Modal>
            </div>
        );
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Checkout);