import React, { Component } from 'react';
import Burger from './Burger/Burger';
import Controls from './Controls/Controls';
import { Modal, ModalBody, ModalHeader, ModalFooter, Button } from 'reactstrap';
import Summary from './Summary/Summary';
import { connect } from 'react-redux';
import { addIngredient, removeIngredient, updatePurchasable } from '../../redux/actionCreators';

const mapStateToProps = state => {
    return {
        ingredients: state.ingredients,
        totalPrice: state.totalPrice,
        purchasable: state.purchasable,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        addIngredient: (igtype) => dispatch(addIngredient(igtype)),
        removeIngredient: (igtype) => dispatch(removeIngredient(igtype)),
        updatePurchasable: () => dispatch(updatePurchasable()),
    }
}
// const INGREDIENT_PRICES = {
//     // state table type name er sathe similer hote hobe
//     salad: 20,
//     cheese: 40,  ////redux have this, so no needed////
//     meat: 90,    //have reducer.js//
// }

class BurgerBuilder extends Component {
    state = {
        // ingredients: [
        //     { type: 'salad', amount: 0 },
        //     { type: 'cheese', amount: 0 }, //have reducer.js//
        //     { type: 'meat', amount: 0 },
        // ],
        // totalPrice: 80,
        modalOpen: false,
        // purchasable: false,
    }

    // updatePurchasable = ingredients => {
    //     const sum = ingredients.reduce((sum, element) => {
    //         return sum + element.amount;       //have reducer.js//
    //     }, 0);
    //     this.setState({ purchasable: sum > 0 })
    // }

    addIngredientHandle = type => {
        // const ingredients = [...this.state.ingredients];
        // const newPrice = this.state.totalPrice + INGREDIENT_PRICES[type];
        // for (let item of ingredients) {   //have reducer.js//
        //     if (item.type === type) item.amount++;
        // }
        // this.setState({ ingredients: ingredients, totalPrice: newPrice });
        // this.updatePurchasable(ingredients);
        this.props.addIngredient(type);  //cll holai redux e chole 
        this.props.updatePurchasable();     //jabe(reduder.js)//
    }

    removeIngredientHandle = type => {
        // const ingredients = [...this.state.ingredients];
        // const newPrice = this.state.totalPrice - INGREDIENT_PRICES[type];
        // for (let item of ingredients) {
        //     if (item.type === type) {
        //         if (item.amount <= 0) return;
        //         item.amount--;
        //     }                        //have reducer.js//
        // }
        // this.setState({ ingredients: ingredients, totalPrice: newPrice });
        // this.updatePurchasable(ingredients);
        this.props.removeIngredient(type);
        this.props.updatePurchasable();
    }

    toggleModal = () => {
        this.setState({
            modalOpen: !this.state.modalOpen,
        })
    }

    handleCheckout = () => {
        this.props.history.push("/checkout");
    }
    // componentDidMount() {
    //     console.log(this.props);
    //     // this.props.history.push("/checkout");
    // }

    render() {
        return (
            <div>
                <div className="d-flex flex-md-row flex-column">
                    <Burger ingredients={this.props.ingredients} />
                    <Controls
                        ingredientAdded={this.addIngredientHandle}
                        ingredientRemoved={this.removeIngredientHandle}
                        price={this.props.totalPrice}
                        toggleModal={this.toggleModal}
                        purchasable={this.props.purchasable}
                    />

                </div>
                <Modal isOpen={this.state.modalOpen}>
                    <ModalHeader>Your Order Summary</ModalHeader>
                    <ModalBody>
                        <h5>Total Price:{this.props.totalPrice.toFixed(0)}</h5>
                        <Summary ingredients={this.props.ingredients} />
                    </ModalBody>
                    <ModalFooter>
                        <Button color="success" style={{ backgroundColor: "#D70F64" }} onClick={this.handleCheckout}>Continue to Checkout</Button>
                        <Button color="secondary" onClick={this.toggleModal}>Cancel</Button>
                    </ModalFooter>
                </Modal>
            </div>

        )
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(BurgerBuilder);